let wx = require('weixin-js-sdk');
/*
 
 
 @param{data}:
 @param{cb}:
 */
export function wexinPay(data,cb,errorCb) {
  let appId = data.appId;
  let timestamp = data.timeStamp;
  let nonceStr = data.nonceStr;
  let signature = data.signature;
  let packages = data.package;
  let paySign = data.paySign;
  wx.config({
    debug: false, // ,apialertpclogpc
    appId: appId, // 
    timestamp: timestamp, // 
    nonceStr: nonceStr, // 
    signature: signature, // 1
    jsApiList: ['chooseWXPay'] // JSJS2
  });
  wx.ready(function(){
    wx.chooseWXPay({
      timestamp: timestamp, // jssdktimestamptimeStampS
      nonceStr: nonceStr, //  32 
      package: packages, // prepay_idprepay_id=***
      signType: 'MD5', // 'SHA1''MD5'
      paySign: paySign, // 
      success: function(res) {
        // 
        console.log(res);
        cb(res);
      },
      fail:function(res){
        errorCb(res);
      }
    });
  });
  wx.error(function(res) {
    // configerrorconfigdebugresSPA
    /*alert("config");*/
  });
}


export  function getLocation(data,cb,errorCb) {
  let appId = data.appId;
  let timestamp = data.timestamp;
  let nonceStr = data.nonceStr;
  let signature = data.signature;
  wx.config({
    debug: false, // ,apialertpclogpc
    appId: appId, // 
    timestamp: timestamp, // 
    nonceStr: nonceStr, // 
    signature: signature, // 1
    jsApiList: ['getLocation'] // JSJS2
  });
  wx.ready(function(){
    wx.getLocation({
      type: 'wgs84',
      success: function(res) {
        // 
        cb(res);
      },
      fail:function(res){
        errorCb(res);
      }
    });
  });
  wx.error(function(res) {
    // configerrorconfigdebugresSPA
    alert("config");
  });
}

export  function openLocation(data,storeInfo,cb,errorCb) {
  let appId = data.appId;
  let timestamp = data.timestamp;
  let nonceStr = data.nonceStr;
  let signature = data.signature;
  wx.config({
    debug: false, // ,apialertpclogpc
    appId: appId, // 
    timestamp: timestamp, // 
    nonceStr: nonceStr, // 
    signature: signature, // 1
    jsApiList: ['openLocation'] // JSJS2
  });
  wx.ready(function(){
    wx.openLocation({
      latitude:storeInfo.latitude,
      longitude: storeInfo.longitude,
      name: storeInfo.storeName,
      address:storeInfo.storeAddress,
      scale: 18,
      success: function () {
//                                            goBack();
      },
      fail:function(res){
        errorCb(res);
      }
    });
  });
  wx.error(function(res) {
    // configerrorconfigdebugresSPA
    alert("config");
  });
}






