<template>
  <yd-layout style="background-color: white">
    <yd-navbar slot="navbar" title="" bgcolor="#d41d0f" color="#FFF">
      <router-link to="" slot="left" @click.native="gotoback()">
        <yd-navbar-back-icon color="#FFF"></yd-navbar-back-icon>
      </router-link>
    </yd-navbar>
    <div class="introduct" style="padding: 25px 15px 5px 15px;">
      <h3 style="color: #4d4d4d;text-align: center;font-weight: bold;font-size: .3rem;"></h3>
      <div class="info" >
        <p style="font-size: .25rem; color: #666666;line-height: .5rem;margin-bottom: .3rem; position: relative;padding-left:.3rem;">1204S<u></u>12</p>
      </div>
    </div>
    <div class="introduct" style="padding: 25px 15px 5px 15px;">
      <h3 style="color: #4d4d4d;text-align: center;font-weight: bold;font-size: .3rem;"></h3>
      <div class="info" >
        <p style="font-size: .25rem; color: #666666;line-height: .5rem;margin-bottom: .3rem; position: relative;padding-left:.3rem;">-</p>
        <p style="font-size: .25rem; color: #666666;line-height: .5rem;margin-bottom: .3rem; position: relative;padding-left:.3rem;">4S,!</p>
      </div>
    </div>
  </yd-layout>
</template>
<script type="text/babel">
  const vm= {
    data() {
      return {

      }
    },
    mounted(){

    },
    methods:{
      gotoback(){
        this.$router.go(-1);
      },
    },

  }
  export default vm;
</script>
<style scoped>
  .content p {
    line-height: 30px;
    font-size: 0.875em;
    color: #585858;
  }
  .introduct .info p::after{
    position: absolute;
    content: "";
    top: 8px;
    left: 0;
    height: 7px;
    width: 7px;
    background-color: #d41d0f;
    border-radius: 5px;
  }
  .info{
    border-left: none;
    padding: 10px 0 10px 0;
  }
</style>



